    `};
`;

export default function BenefitSection() {
  const { language } = useI18next();
  const { t } = useTranslation();
  const [currentModal, setCurrentModal] = useState<{
    show: boolean;
    data: {
      key: null | number;
      title: string;
      description: string;
      img: any;
      img_2x: any;
    };
  }>({
    show: false,
    data: {
      key: null,
      title: "",
      description: "",
      img: null,
      img_2x: null,
    },
  });

  const handleOnClick = (key: number | null) => {
    if (key || key === 0) {
      setCurrentModal({
        show: true,
        data: {
          key: key,
          title: (WelfareData as any)[key].title,
          description: (WelfareData as any)[key].description,
          img: (WelfareData as any)[key].img,
          img_2x: (WelfareData as any)[key].img_2x,
        },
      });
    } else {
      setCurrentModal({
        ...currentModal,
        show: false,
      });
    }
  };

  const handleArrowClick = (key: number | null) => {
    if (key || key === 0) {
      setCurrentModal({
        show: true,
        data: {
          key: key,
          title: (WelfareData as any)[key].title,
          description: (WelfareData as any)[key].description,
          img: (WelfareData as any)[key].img,
          img_2x: (WelfareData as any)[key].img_2x,
        },
      });
    }
  };

  return (
    <ContainerStyled>
      <SubTitle
        data-sal="slide-up"
        data-sal-duration="1000"
        data-sal-easing="ease"
      >
        {t("HPG-55")}
      </SubTitle>
      <Wrapper
        data-sal="slide-up"
        data-sal-duration="1000"
        data-sal-easing="ease"
      >
        {Object.keys(WelfareData).map(function (key, index) {
          if (key == "1" && language === "en") {
            return;
          }
          return (
            <BenefitItemButton
              key={key}
              onClick={
                language === "ko"
                  ? () => handleOnClick(parseInt(key))
                  : () => {}
              }
              lang={language}
            >
              {(WelfareData as any)[key].title}
            </BenefitItemButton>
          );
        })}
      </Wrapper>
      {currentModal.show && (
        <Modal
          data={currentModal.data}
          onClick={(e) => handleOnClick(e)}
          onArrowClick={(e) => handleArrowClick(e)}
        />
      )}
    </ContainerStyled>
  );
}
